@import "./src/scss/init";






































































.comment {
    width: 100%;
    padding-bottom: px2html(100px);
    position:relative;
    top:px2html(-125px);
    .head-title {
        display: flex;
        align-items: center;
        color: rgba(154, 129, 117, 1);
        font-size: px2html(14px);
        height: px2html(45px);
        text-align: left;
        box-sizing: border-box;
        padding-left: px2html(25px);
        background-color: #fff;
    }
    .nothing {
        font-size: px2html(14px);
        color: rgba(87, 49, 30, 0.6);
        background-color: #fff;
        padding-bottom: px2html(10px);
    }
    .comment-template {
        background-color: rgba(255, 255, 252, 1);
        height: px2html(99px);
        width: 100%;
        position: relative;
        .head-img {
            width: px2html(35px);
            height: px2html(35px);
            border-radius: 50%;
            position: absolute;
            left: px2html(25px);
            top: px2html(14px);
        }
        .user-name {
            position: absolute;
            left: px2html(69px);
            top: px2html(15px);
            color: rgba(87, 49, 30, 0.4);
            font-size: px2html(14px);
            height: px2html(14px);
        }
        .date {
            position: absolute;
            top: px2html(35px);
            left: px2html(70px);
            height: px2html(12px);
            color: rgba(87, 49, 30, 0.2);
            font-size: px2html(12px);
        }
        .comment-content {
            text-align: justify;
            width: px2html(281px);
            position: relative;
            top: px2html(54px);
            color: rgba(87, 49, 30, 0.6);
            font-size: px2html(14px);
            left: px2html(70px);
        }
        .like-count {
            position: absolute;
            right: px2html(25px);
            top: px2html(30px);
            display: flex;
            align-items: center;
            .count {
                position: relative;
                left: px2html(-5px);
                color: rgba(87, 49, 30, 0.2);
                font-size: px2html(12px);
            }
            .like-icon {
                position: relative;
                top: px2html(-1px);
                width: px2html(20px);
                opacity: .2;
            }
        }
    }
    .bottom-button {
        position: relative;
        left: 50%;
        top: px2html(15px);
        transform: translateX(-50%);
        color: rgba(87, 49, 30, 0.6);
        width: px2html(118px);
        height: px2html(30px);
        font-size: px2html(14px);
        border-radius: px2html(15px);
        border: px2html(1px) rgba(87, 49, 30, 0.6) solid;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
